/* eslint-disable react/no-unknown-property */
/* eslint-disable no-nested-ternary */
/* eslint-disable jsx-a11y/label-has-associated-control */
/* eslint-disable react/jsx-props-no-spreading */
import { useForm } from 'react-hook-form';

import styled from 'styled-components';

const Container = styled.div`
  display: flex;
  margin: auto;
  width: 60%;
  flex-direction: column;
  margin-right: 6em;
  text-align: center;
`;

const Title = styled.h2`
  font-size: 1.4em;
  margin-bottom: 1.2em;
  font-weight: bold;
`;

const Form = styled.form`
  display: flex;
  flex-direction: column;

  label {
    font-size: 1.1em;
    text-align: start;
    margin-bottom: 0.5em;
    color: #CCC;
  }

  input {
    padding: 1em 1em;
    margin-bottom: 1.2em;
  }
`;

const Error = styled.p`
  text-align: start;
  margin-bottom: 1.2em;
  color: red;
`;

const RegisterButton = styled.button`
  margin-top: 1em;
  padding: 1em 0;
  color: #FFF;
`;

export default function SignUp({ submit, errorMessage, isExistingUserId }) {
  const {
    register, watch, handleSubmit, formState: { errors },
  } = useForm({ reValidateMode: 'onSubmit' });

  const onSubmit = async (data) => {
    submit(data);
  };

  return (
    <Container>
      <Title>SIGN UP</Title>
      <Form onSubmit={handleSubmit(onSubmit)}>
        <label htmlFor="input-name"></label>
        <input
          id="input-name"
          type="text"
          placeholder=" (2 ~ 10)"
          errors={errors.name}
          {...register('name', {
            required: { value: true, message: ' .' },
            pattern: { value: /^[-a-z0-9]{2,10}$/, message: '  ' },
          })}
        />
        {errors.name ? (
          <Error>{errors.name.message}</Error>
        ) : (
          null
        )}
        <label htmlFor="input-identification"></label>
        <input
          id="input-identification"
          type="text"
          placeholder=" 4 ~ 16    ."
          errors={errors.identification}
          {...register('identification', {
            required: { value: true, message: ' .' },
            pattern: { value: /^[a-z0-9]{4,16}$/, message: '  ' },
          })}
        />
        {errors.identification ? (
          <Error>{errors.identification.message}</Error>
        ) : isExistingUserId ? (
          <Error>{errorMessage}</Error>
        ) : null}
        <label
          htmlFor="input-password"
        >
          
        </label>
        <input
          id="input-password"
          type="password"
          placeholder="8  (), ,    ."
          error={errors.password}
          {...register('password', {
            required: { value: true, message: ' ' },
            pattern: {
              value: /(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}/,
              message: '  ',
            },
          })}
        />
        {errors.password ? (
          <Error>{errors.password.message}</Error>
        ) : (
          null
        )}
        <label
          htmlFor="input-confirmPassword"
        >
           
        </label>
        <input
          id="input-confirmPassword"
          type="password"
          placeholder=" "
          error={errors.confirmPassword}
          {...register('confirmPassword', {
            required: true,
            validate: (value) => value === watch('password'),
          })}
        />
        {errors.confirmPassword ? (
          <Error>  </Error>
        ) : null}
        <RegisterButton
          id="signup"
          type="submit"
        >
          
        </RegisterButton>
      </Form>
    </Container>
  );
}
